import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import { GoogleApiWrapper, InfoWindow, Map, Marker } from 'google-maps-react';
import Maps from '../../GoogleMaps/Maps';
import { Container } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
    },
    imgLoc: {
      [theme.breakpoints.down('sm')]: {
        width:'150px', 
        height:'150px', 
        marginLeft: '20px'
      },
      [theme.breakpoints.up('md')]: {
      },
      [theme.breakpoints.up('lg')]: {
        width:'450px', 
        height:'450px', 
        border:'1 px solid grey', 
        marginLeft: '85px'
      },
      [theme.breakpoints.up('xl')]: {
        width:'550px', 
        height:'550px', 
        border:'1 px solid grey', 
        marginLeft: '200px'
      },
    },
    headLoc:{
      [theme.breakpoints.down('sm')]: {
        marginLeft: '20px'
      },
      [theme.breakpoints.up('md')]: {
      },
      [theme.breakpoints.up('lg')]: {
        marginLeft:'85px', 
        borderBottom:'1px solid grey'
      },
      [theme.breakpoints.up('xl')]: {
        marginLeft:'200px', 
        borderBottom:'1px solid grey',
        width:'48%',
        fontSize:'30px'
      },
    },
    txtLoc: {
      [theme.breakpoints.down('sm')]: {
        marginLeft: '20px'
      },
      [theme.breakpoints.up('md')]: {
      },
      [theme.breakpoints.up('lg')]: {
        marginLeft:'85px'
      },
      [theme.breakpoints.up('xl')]: {
        marginLeft:'200px',
        fontSize:'30px'
      },
    },
    greyGrid: {
        [theme.breakpoints.down('sm')]: {
          backgroundColor: '#F3F5F4',
          marginTop: '80px'
        },
        [theme.breakpoints.up('md')]: {
          backgroundColor: '#F3F5F4',
        },
        [theme.breakpoints.up('lg')]: {
          backgroundColor: '#F3F5F4',
          marginTop: '80px',
        },
        [theme.breakpoints.up('xl')]: {
          backgroundColor: '#F3F5F4',
          marginTop: '130px',
        },
    },
    whiteGrid: {
        [theme.breakpoints.down('sm')]: {
          backgroundColor: '#FFFFFF',
          marginTop: '80px'
        },
        [theme.breakpoints.up('md')]: {
          backgroundColor: '#F3F5F4',
        },
        [theme.breakpoints.up('lg')]: {
          backgroundColor: '#FFFFFF',
          marginTop: '80px',
        },
    },
    heading: {
        [theme.breakpoints.down('sm')]: {
          color: '#006936',
          marginLeft: '25px',
        },
        [theme.breakpoints.up('md')]: {
          color: '#006936'
        },
        [theme.breakpoints.up('lg')]: {
          color: '#006936',
          marginLeft: '25px'
        },
        [theme.breakpoints.up('xl')]: {
          color: '#006936',
          marginLeft: '50px',
          fontSize:'36px'
        },
    },
    textGreyGrid: {
        [theme.breakpoints.down('sm')]: {
          marginLeft:'25px'
        },
        [theme.breakpoints.up('md')]: {
        },
        [theme.breakpoints.up('lg')]: {
            marginLeft:'25px'
        },
        [theme.breakpoints.up('xl')]: {
            marginLeft:'50px',
            fontSize:'30px'
        },
    }
}));

export default function Contact(){
    const classes = useStyles();

    return(
    <div className={classes.root}>
      <Grid container className={classes.greyGrid}>
          <Grid item xs={12}>
            <h2 className={classes.heading}>Contact</h2>
            <p className={classes.textGreyGrid}>
            All of our Offices are opens 8.00 to 17.00 WIB
            </p>
          </Grid>
          <Grid item xs={6}>
            <img src='./Assets/contact-maps.png' className={classes.imgLoc}/>
            <p className={classes.headLoc}>Jakarta Office</p>
            <p className={classes.txtLoc}>
              Jl. Cempaka Putih Tengah II No. 2D,<br/>
              Jakarta Pusat 10510<br/>
              Whatsapp: +62 81357575757<br/>
              Fax: 021-4203122<br/>
              Email: info@gledex.co.id<br/>
            </p>
          </Grid>
          <Grid item xs={6}>
            <img src='./Assets/contact-maps.png' className={classes.imgLoc}/>
            <p className={classes.headLoc}>Jakarta Office</p>
            <p className={classes.txtLoc}>
              Jl. Cempaka Putih Tengah II No. 2D,<br/>
              Jakarta Pusat 10510<br/>
              Whatsapp: +62 81357575757<br/>
              Fax: 021-4203122<br/>
              Email: info@gledex.co.id<br/>
            </p>
          </Grid>
          <Grid item xs={6}>
            <img src='./Assets/contact-maps.png' className={classes.imgLoc}/>
            <p className={classes.headLoc}>Jakarta Office</p>
            <p className={classes.txtLoc} >
              Jl. Cempaka Putih Tengah II No. 2D,<br/>
              Jakarta Pusat 10510<br/>
              Whatsapp: +62 81357575757<br/>
              Fax: 021-4203122<br/>
              Email: info@gledex.co.id<br/>
            </p>
          </Grid>
          <Grid item xs={6}>
            <img src='./Assets/contact-maps.png' className={classes.imgLoc}/>
            <p className={classes.headLoc}>Jakarta Office</p>
            <p className={classes.txtLoc} >
              Jl. Cempaka Putih Tengah II No. 2D,<br/>
              Jakarta Pusat 10510<br/>
              Whatsapp: +62 81357575757<br/>
              Fax: 021-4203122<br/>
              Email: info@gledex.co.id<br/>
            </p>
          </Grid>
      </Grid>
    </div>
    );
}