import * as React from 'react';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import { makeStyles } from '@material-ui/core/styles';
import { Link } from '@mui/material';


const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 0
    },
    footerBio: {
      color:'white', 
      textAlign:'left',
      fontSize: '22px',
      fontFamily:'Roboto',
      marginLeft:'100px'
    },
    footerCopyRight: {
      color:'#FFFFFF',
      fontSize: '18px',
      fontFamily:'Roboto',
      marginLeft:'100px'
    },
    footerHeading: {
      [theme.breakpoints.between('xs','sm')]: {

      },
      [theme.breakpoints.between('sm','md')]: {
      },
      [theme.breakpoints.up('lg')]: {

      },
      [theme.breakpoints.up('xl')]: {
        color:'white',
        fontSize:'18px',
        fontFamily:'Roboto'
      },
    },
    footerText: {
      [theme.breakpoints.between('xs','sm')]: {

      },
      [theme.breakpoints.between('sm','md')]: {
      },
      [theme.breakpoints.up('lg')]: {

      },
      [theme.breakpoints.up('xl')]: {
        color:'white',
        fontSize:'16px',
        fontFamily:'Roboto'
      },
    },
}));

const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

export default function FootBar() {
  const classes = useStyles();

  return (
    <footer>
    <Grid container style={{marginTop:'23px', width: '100%'}}>
        <Grid item xs={12} style={{marginLeft:'50px'}}>
            <img src='./Assets/gledex-logo.png'/>
        </Grid>
    </Grid>
    <Box sx={{ flexGrow: 1 }} bgcolor='#006936' style={{width: '100%', marginTop: '20px'}}>
      <Grid container spacing={2}>
        <Grid item xs={4}>
          <Box>
            <p className={classes.footerBio}>Puasnya Pelanggan Adalah Nafas Kami, Tetap Profesional, Layani Dengan Hati.
            Halangan Dengan Semangat Tinggi Demi Visi Kami.
            Terus Berinovasi Dengan Dedikasi Tinggi.
            GLEDEX LOGISTICS, Cepat Dan Tepat</p>
          </Box>
        </Grid>
        <Grid item xs={8} style={{display:'inline'}}>
          <Grid   
            container
            direction="row"
            justifyContent="flex-end"
            columnGap={10}
            style={{marginLeft:'-100px'}}>
              <Grid item>
                <Box>
                  <h4 className={classes.footerHeading}>Services</h4>
                </Box>
                <Box>
                  <Link href='#'>
                      <span className={classes.footerText}>Services List</span>
                  </Link>
                </Box>
                <Box>
                  <Link href='/armada'>
                    <span className={classes.footerText}>Armada</span>
                  </Link>
                </Box>
              </Grid>  
              <Grid item>
                <Box>
                  <h4 className={classes.footerHeading}>Gallery</h4>
                </Box>
                <Box>
                  <Link href='#'>
                    <span className={classes.footerText}>Projects</span>
                  </Link>
                </Box>
                <Box>
                  <Link href='#'>
                    <span className={classes.footerText}>Gathering</span>
                  </Link>
                </Box>
                <Box>
                  <Link href='#'>
                    <span className={classes.footerText}>CSR</span>
                  </Link>
                </Box>
              </Grid>  
              <Grid item>
                <Box>
                  <h4 className={classes.footerHeading}>About</h4>
                </Box>
                <Box>
                  <Link href='/aboutus'>
                    <span className={classes.footerText}>Overview</span>
                  </Link>
                </Box>
                <Box>
                  <Link href='/structure'>
                    <span className={classes.footerText}>Structure and Legal</span>
                  </Link>
                </Box>
              </Grid>  
              <Grid item>
                <Box>
                  <h4 className={classes.footerHeading}>Contact Us</h4>
                </Box>
                <Box>
                  <Link href='#'>
                      <div style={{
                          display: 'flex',
                          alignItems: 'center',
                          flexWrap: 'wrap',
                      }}>
                          {/* <WhatsAppIcon/> */}
                          <span className={classes.footerText}>WhatsApp</span>
                      </div>
                  </Link>
                </Box>
                <Box>
                  <Link href='#'>
                      <div style={{
                          display: 'flex',
                          alignItems: 'center',
                          flexWrap: 'wrap',
                      }}>
                          {/* <InstagramIcon/> */}
                          <span className={classes.footerText}>Instagram</span>
                      </div>                
                  </Link>
                </Box>
                <Box>
                  <Link href='#'>
                      <div style={{
                          display: 'flex',
                          alignItems: 'center',
                          flexWrap: 'wrap',
                      }}>
                          {/* <LinkedInIcon/> */}
                          <span className={classes.footerText}>LinkedIn</span>
                      </div>              
                  </Link>
                </Box>            
                <Box>
                  <Link href='#'>
                      <div style={{
                          display: 'flex',
                          alignItems: 'center',
                          flexWrap: 'wrap',
                      }}>
                          {/* <FacebookIcon/> */}
                          <span className={classes.footerText}>Facebook</span>
                      </div> 
                  </Link>
                </Box>
              </Grid>  
          </Grid>              
        </Grid>
      </Grid>
      <Grid container spacing={5} style={{marginBottom:'50px'}}>
        <Grid item>
          <p className={classes.footerCopyRight}>©2021 Gledex Logistics. All rights reserved</p>
        </Grid>
      </Grid>
    </Box>
    </footer>
  );
}
