import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Paper, Button, Box, Link, Container } from '@material-ui/core'
import Grid from '@material-ui/core/Grid';
import Carousel from 'react-material-ui-carousel'
import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import InstagramIcon from '@material-ui/icons/Instagram';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import FacebookIcon from '@material-ui/icons/Facebook';


const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 0
    },
    footerBio: {
      color:'white', 
      textAlign:'left',
      fontSize: '22px',
      fontFamily:'Roboto',
      marginLeft:'200px'
    },
    footerCopyRight: {
      color:'#FFFFFF',
      fontSize: '18px',
      fontFamily:'Roboto',
      marginLeft:'200px'
    },
    footerHeading: {
      [theme.breakpoints.between('xs','sm')]: {

      },
      [theme.breakpoints.between('sm','md')]: {
      },
      [theme.breakpoints.up('lg')]: {

      },
      [theme.breakpoints.up('xl')]: {
        color:'white',
        fontSize:'18px',
        fontFamily:'Roboto'
      },
    },
    footerText: {
      [theme.breakpoints.between('xs','sm')]: {

      },
      [theme.breakpoints.between('sm','md')]: {
      },
      [theme.breakpoints.up('lg')]: {

      },
      [theme.breakpoints.up('xl')]: {
        color:'white',
        fontSize:'16px',
        fontFamily:'Roboto'
      },
    },
}));

export default function Footer(){
    const classes = useStyles();

    return(
    <footer>
      <Grid container style={{marginTop:'23px', width: '100%'}}>
        <Grid item xs={12} style={{marginLeft:'50px'}}>
            <img src='./Assets/gledex-logo.png'/>
        </Grid>
      </Grid>
      <Box bgcolor='#006936' style={{width: '100%', marginTop: '20px'}}> 
        <Container maxWidth='xl'>
          <Grid container spacing={5} style={{marginBottom:'100px', border:'1px solid grey'}}>
            <Grid item xs={8} sm={4}>
              <Box>
                <p className={classes.footerBio}>Puasnya Pelanggan Adalah Nafas Kami, Tetap Profesional, Layani Dengan Hati.
                Halangan Dengan Semangat Tinggi Demi Visi Kami.
                Terus Berinovasi Dengan Dedikasi Tinggi.
                GLEDEX LOGISTICS, Cepat Dan Tepat</p>
              </Box>
            </Grid>
            <Grid item xs={4} sm={2}>
              <Box>
                <h4 className={classes.footerHeading}>Services</h4>
              </Box>
              <Box>
                <Link href='#' className={classes.footerText}>
                    Services List
                </Link>
              </Box>
              <Box>
                <Link href='/armada' className={classes.footerText}>
                    Armada
                </Link>
              </Box>
            </Grid>
            <Grid item xs={4} sm={2}>
              <Box>
                <h4 className={classes.footerHeading}>Gallery</h4>
              </Box>
              <Box>
                <Link href='#' className={classes.footerText}>
                    Projects
                </Link>
              </Box>
              <Box>
                <Link href='#' className={classes.footerText}>
                    Gathering
                </Link>
              </Box>
              <Box>
                <Link href='#' className={classes.footerText}>
                    CSR
                </Link>
              </Box>
            </Grid>
            <Grid item xs={4} sm={2}>
              <Box>
                <h4 className={classes.footerHeading}>About</h4>
              </Box>
              <Box>
                <Link href='/aboutus' className={classes.footerText}>
                    Overview
                </Link>
              </Box>
              <Box>
                <Link href='/structure' className={classes.footerText}>
                    Structure and Legal
                </Link>
              </Box>          
            </Grid>
            <Grid item xs={4} sm={2}>
              <Box>
                <h4 className={classes.footerHeading}>Contact Us</h4>
              </Box>
              <Box>
                <Link href='#' className={classes.footerText}>
                    <div style={{
                        display: 'flex',
                        alignItems: 'center',
                        flexWrap: 'wrap',
                    }}>
                        {/* <WhatsAppIcon/> */}
                        <span>Whatsapp</span>
                    </div>
                </Link>
              </Box>
              <Box>
                <Link href='#' className={classes.footerText}>
                    <div style={{
                        display: 'flex',
                        alignItems: 'center',
                        flexWrap: 'wrap',
                    }}>
                        {/* <InstagramIcon/> */}
                        <span>Instagram</span>
                    </div>                
                </Link>
              </Box>
              <Box>
                <Link href='#' className={classes.footerText}>
                    <div style={{
                        display: 'flex',
                        alignItems: 'center',
                        flexWrap: 'wrap',
                    }}>
                        {/* <LinkedInIcon/> */}
                        <span>LinkedIn</span>
                    </div>              
                </Link>
              </Box>            
              <Box>
                <Link href='#' className={classes.footerText}>
                    <div style={{
                        display: 'flex',
                        alignItems: 'center',
                        flexWrap: 'wrap',
                    }}>
                        {/* <FacebookIcon/> */}
                        <span>Facebook</span>
                    </div> 
                </Link>
              </Box>     
            </Grid>
          </Grid>
          <Grid container spacing={5} style={{marginBottom:'100px'}}>
            <Grid item>
              <p className={classes.footerCopyRight}>©2021 Gledex Logistics. All rights reserved</p>
            </Grid>
          </Grid>
        </Container>
      </Box>
    </footer>
    )
}

