const AntTab = withStyles((theme) => ({
  root: {
    textTransform: "none",
    minWidth: 72,
    fontWeight: theme.typography.fontWeightRegular,
    marginRight: theme.spacing(4),
    color:'#006936',
    fontFamily: [
      "-apple-system",
      "BlinkMacSystemFont",
      '"Segoe UI"',
      "Roboto",
      '"Helvetica Neue"',
      "Arial",
      "sans-serif",
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"'
    ].join(","),
    // "&:hover": {
    //   color: "#40a9ff",
    //   opacity: 1
    // },
    "&$selected": {
      color: "#006936",
      fontWeight: theme.typography.fontWeightMedium
    },
    "&:focus": {
      color: "#006936"
    }
  },
  selected: {}
}))((props) => <Tab disableRipple {...props} />);




